<template>
  <div class="flex flex-col items-center pt-32">
    <div class="w-full max-w-xs">
      <form class="bg-white shadow rounded px-8 pt-6 pb-8 mb-4">

        <h1 class="mb-4 text-xl text-center">HR App</h1>
        <hr class="mb-4">

        <div class="mb-4">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="username">
            Username
          </label>
          <input v-model="username" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" id="username" type="text" placeholder="Username">
        </div>
        <div class="mb-6">
          <label class="block text-gray-700 text-sm font-bold mb-2" for="password">
            Password
          </label>
          <input v-model="password" class="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline" id="password" type="password" placeholder="******************">
        </div>
        <div class="flex items-center justify-between">
          <button @click="login" class="bg-teal-500 hover:bg-teal-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" type="button">
            Sign In
          </button>
          <a class="inline-block align-baseline font-bold text-sm text-teal-500 hover:text-teal-800" href="javascript:void(0)">
            Forgot Password?
          </a>
        </div>
      </form>
      <p class="text-center text-gray-500 text-xs">
        &copy;2020 Your Corp. All rights reserved.
      </p>
    </div>
  </div>
</template>

<script>
  import { mapState, mapActions, mapGetters, mapMutations } from 'vuex'
  import Cookies from 'js-cookie'

  export default {
    name: 'Login',

    data: () => ({
      username: 'admin',
      password: 'admin',
    }),

    computed: {
      ...mapState([]),
      ...mapGetters([]),
    },

    methods: {
      ...mapActions({
        authorize: 'auth/login'
      }),
      ...mapMutations([]),
      login () {
        const credentials = {
          username: this.username,
          password: this.password,
        }
        this.authorize(credentials)
      }
    }
  }
</script>
